@use './partials/_variables';
@use './partials/_mixins';

@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;0,900;1,400;1,700;1,900&display=swap');

body {
  margin: 0;
  line-height: 1;
  font-family: 'Lato', sans-serif;
  font-size: 100%;

  & * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
}

h1 {
  font-size: variables.$font-size-heading-large;
  font-weight: variables.$font-weight-bold;
  line-height: variables.$line-height-heading-large;
}

h2 {
  font-size: variables.$font-size-heading-medium;
  font-weight: variables.$font-weight-medium;
  line-height: variables.$line-height-heading-medium;
}

h3 {
  font-size: variables.$font-size-heading-small;
  font-weight: variables.$font-weight-medium;
  line-height: variables.$line-height-heading-small;
}

p,
a {
  font-size: variables.$font-size-paragraph;
  line-height: variables.$line-height-paragraph;
}
